import React, { useState } from 'react';
import styled from 'styled-components';
import useBooks from '../Hooks/useBooks';
import useActions from '../Hooks/useActions';
import ShoppingCartCheckoutIcon from '@mui/icons-material/ShoppingCartCheckout';
import AddCircleOutlineIcon from '@mui/icons-material/AddCircleOutline';
const BookList = () => {
  const { books} = useBooks();
  const { addToOrder } = useActions();

  return (
    <BookListWrapper>
      {books.map((book) => {
        const {isbn, thumbnail, title, price, sale_price, publisher, url } =
          book;
          console.log(books)

        const click = () => {
          addToOrder(isbn);
      
        };


        return (
          <BookItem key={isbn}>
            <div className='bookImg'>
              <a href={url} target='_blank' rel='noreferrer'>
                <img src={thumbnail ? thumbnail:'http://via.placeholder.com/120X150'} alt={thumbnail} />
              </a>
            </div>
            <div className='bookContents'>
              <div className='book-txt'>
              <div className='bookPublisher'>{publisher}</div>
              <div className='bookTitle'>{title}</div>
              <div className='bookPrice'>
                &#8361; {sale_price.toLocaleString()}

                <strike>{price.toLocaleString()}</strike>{' '}
              </div>
              <div className='icons'>
              <ShoppingCartCheckoutIcon onClick={click} />
                <a href={url} target='_blank' rel='noreferrer'>
                <AddCircleOutlineIcon/>
                </a>
              </div>
              </div>
            </div>
          </BookItem>
        );
      })}
    </BookListWrapper>
  );
};

const BookListWrapper = styled.li`
  background-color: var(--grey-color);
  display: grid;
  width: 60%;
  min-height: 40rem;
  margin: auto;
  grid-gap: 3rem;
  grid-template-columns: repeat(3, 1fr);
`;
const BookItem = styled.dl`
  position: relative;
  width: 100%;
  cursor: pointer;
  overflow: hidden;
  .book-txt{
    padding: 20px;
  }
  .bookImg {
    img {
      width: 100%;
    }
  }
  .bookContents {
    position: absolute;
    text-align: left;
    
    position: absolute;
    bottom: 0;
    margin: 0 auto;
    background-color: rgba(115, 120, 152,0.6);
  
    opacity: 0;
    visibility: visible;
    width: 100%;
  }
  &:hover {
    .bookImg {
      img {
        opacity: 0.5;
      }
    }
    .bookContents {
      opacity: 1;
      transition: all 0.6s 0s;
      .bookPublisher {
        margin-bottom: 0.5rem;
        font-size: 1rem;
        font-weight: 300;
        color: var(--darkgrey-color);
      }
      .bookTitle {
        margin-bottom: 1rem;
        font-size: 1.2rem;
        font-weight: 600;
        line-height: 25px;
        color: var(--white-color);
      }
      .bookPrice {
        font-size: 1.1rem;
        font-weight: 600;
        line-height: 25px;
        color: var(--white-color);
        strike {
          color: var(--darkgrey-color);
          font-size: 1rem;
          margin-left: 0.9rem;
        }
      }
      .icons {
        display: flex;
        cursor: pointer;
        justify-content: flex-end;
    
        }
      }
    }
  }
`;

export default BookList;