import React, { useCallback, useEffect, useState } from 'react';
import { bookSearch } from '../api';
import AppStateContext from '../contexts/AppStateContext';
import { all } from 'axios';


const AppStateProvider = ({ children }) => {
  const [books, setBooks] = useState([]);
  const [query, setQuery] = useState(''); 
  //  
  useEffect(() => {
    if (query.length > 0) {
      bookSearchHttpHandler(query, true); //   ,  .
    }
  }, [query]);

  // book search 
  const bookSearchHttpHandler = async (query, reset) => {
    // paramter 
    const params = {
      query: query,
      sort: 'accuracy',
      page: 1,
      size: 9,
       // accuracy | recency  or 
   // 
      //      
    };
   
  
    const { data } = await bookSearch(params); // api 
    if (reset) {
      setBooks(data.documents);
    

    } else {
      setBooks(books.concat(data.documents))
    }
  };
  const searchBook = (text) => {
    setQuery(text);

  };

  const [orders, setOrders] = useState([]);

  //  
  // [{isbn, quantity : 1}]
  const addToOrder = useCallback((isbn) => {
    setOrders((orders) => {
      //     2, 3     isbn  
      const finded = orders.find((order) => order.isbn === isbn);
      //     quantity 1 
      if (finded === undefined) {
        return [...orders, { isbn, quantity: 1 }];
      } //   
      else {
        return orders.map((order) => {
          if (order.isbn === isbn) {
            return {
              isbn,
              quantity: order.quantity + 1,
            };
          } else {
            return order;
          }
        });
      }
    });
  }, []);

  //   
  const remove = useCallback((isbn) => {
    setOrders((orders) => {
      return orders.filter((order) => order.isbn !== isbn);
    });
  }, []);

  //    
  const removeAll = useCallback(() => {
    setOrders([]);
  }, []);

  return (

    <AppStateContext.Provider
      value={{
        books,
        searchBook,
        orders,
        addToOrder,
        remove,
        removeAll,
      }}>
      {children}
    </AppStateContext.Provider>
  );
};

export default AppStateProvider;